.advantage {
    text-align: center;
    margin-bottom: 58px;

    &-img {
        margin: 0 auto 22px;

        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;

        width: 93px;
        height: 93px;
        border-radius: 50%;
        background-color: $secondary-color;
        object-fit: scale-down;
        padding: 15px;

        &::before {
            content: "";
            max-width: 113px;
            min-width: 113px;
            width: 100%;
            height: 113px;
            border-radius: 50%;
            border: 1px solid #6C7188;
            position: absolute;
            transform: translate(-50%, -50%);
            top: 50%;
            left: 50%;
        }

    }

    &-title {
        font-size: 18px;
        line-height: 124.5%;
    }
}

#slider-advantages {
    margin: 90px 0 -58px;
}
